<template>
  <div class="horizontal-tap">
    <ul>
      <li v-for="(obj, index) in data" v-bind:key="obj.name" v-bind:class="index == current ? 'active' : ''">
        <a class="item-name" @click="activeItem(index,obj.name)">{{obj.name}}</a>
        <span class="current" v-if="index == current"></span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'HorizontalTap',
  props: {
    data: Array,
    active: Number
  },
  data () {
    return {
      current: this.active
    }
  },
  methods: {
    activeItem (index, name) {
      if (this.data[index].url) {
        if (this.data[index].type !== '_blank') { // 打开新窗口时不改变页面tab颜色
          this.current = index
        }
        window.open(this.data[index].url, this.data[index].type)
        // window.location = this.data[index].url
      } else {
        this.current = index
        this.$emit('activeItem', index, name)
      }
    }
  }
}
</script>
<style scoped lang="scss">
  .horizontal-tap{
    overflow: auto;
    z-index:100;
    background-color: #666;
    ul{
      padding-left: 0;
      margin-bottom: 0;
      list-style: none;
      li+li{
        margin-left:56px;
      }
      li{
        position: relative;
        display: block;
        float:left;
        a{
          text-decoration: none;
          position: relative;
          display: block;
          padding: 14px 0;
          text-align: center;
          font-size:16px;
          font-family:PingFang-SC-Bold;
          font-weight:bold;
          /*color:#333333;       */
          color:#F8F8F8;
          cursor: pointer;
        }
      }
      .active{
        .current{
          width:32px;
          height:2px;
          background:rgba(102,18,207,1);
          border-radius:1px;
          display:block;
          margin:0 auto;
        }
        a{
          color: #6612CF;
        }
      }
    }
  }
</style>
