import axios from 'axios'
import baseUrl from '@/utils/setBaseUrl'
import snsUrl from '@/utils/setSnsUrl'
import '@/plugins/element.js'
import { MessageBox, Loading } from 'element-ui'
const TIME_OUT_MS = 60 * 1000 // 默认请求超时时间

/*
 * @param response 返回数据列表
 */
function handleResults (response) {
  let remoteResponse = response.data
  let result = {
    success: false,
    errorCode: '',
    errorMsg: '',
    data: null
  }
  if (remoteResponse.code === '0000') {
    result.success = true
    result.data = remoteResponse.data
  } else {
    result.success = false
    result.errorCode = remoteResponse.code
    result.errorMsg = remoteResponse.msg
    if (remoteResponse.code === '0002') {
      MessageBox.alert('请重新登录', '提示', {
        confirmButtonText: '确定',
        callback: action => {
          window.location = 'login.html'
        }
      })
    } else if (remoteResponse.msg) {
      MessageBox.alert(remoteResponse.msg, '提示', {
        confirmButtonText: '确定'
      })
    }
  }
  return result
}

function handleUrl (url) {
  url = baseUrl + url
  // BASE_URL是接口的ip前缀，比如http:10.100.1.1:8989/
  return url
}

/*
 * @param data 参数列表
 * @return
 */
function handleParams (data) {
  return data
}
export default {
  /*
   * @ 社区URL
   */
  snsUrl,
  /*
   * @param url
   * @param data
   * @param response 请求成功时的回调函数
   * @param exception 异常的回调函数
   */
  snspost (url, data, response, exception) {
    axios({
      method: 'post',
      url: snsUrl + url,
      data: data,
      dataType: 'json',
      processData: false,
      contentType: false,
      timeout: TIME_OUT_MS,
      headers: {
      }
    }).then(
      (result) => {
        response(result)
      }
    ).catch(
      (error) => {
        // 设置不能登录社区flag
        localStorage.setItem('canLoginSns', '1')
        if (localStorage.getItem('userType') === '0') {
          window.location = 'personal-main.html'
        } else {
          window.location = 'assistant-main.html'
        }
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
      }
    )
  },
  /*
   * @param url
   * @param data
   * @param response 请求成功时的回调函数
   * @param exception 异常的回调函数
   */
  post (url, data, response, exception) {
    let token = localStorage.getItem('token')
    // 加载中gif，如需显示请联系本人老于：13224135945
    // const loading = Loading.service({
    //   lock: true,
    //   text: 'Loading',
    //   spinner: 'el-icon-loading',
    //   // background: 'rgba(0, 0, 0, 0.7)'
    //   background: 'rgba(f, 0, 0, 0.7)'
    // })
    axios({
      method: 'post',
      url: handleUrl(url),
      data: handleParams(data),
      timeout: TIME_OUT_MS,
      headers: {
        token: token,
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }).then(
      (result) => {
        // setTimeout(() => {
        //   loading.close()
        // }, 1000)
        response(handleResults(result))
      }
    ).catch(
      (error) => {
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
      }
    )
  },
  /*
   * get 请求
   * @param url
   * @param response 请求成功时的回调函数
   * @param exception 异常的回调函数
   */
  get (url, response, exception) {
    axios({
      method: 'get',
      url: handleUrl(url),
      timeout: TIME_OUT_MS,
      headers: {
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }).then(
      (result) => {
        response(handleResults(result))
      }
    ).catch(
      (error) => {
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
      }
    )
  },
  /*
   * 导入文件
   * @param url
   * @param data
   * @param response 请求成功时的回调函数
   * @param exception 异常的回调函数
   */
  uploadFile (url, data, response, exception) {
    let token = localStorage.getItem('token')
    const loading = Loading.service({
      lock: true,
      text: '努力上传中...',
      spinner: 'el-icon-loading',
      // background: 'rgba(0, 0, 0, 0.7)'
      background: 'rgba(f, 0, 0, 0.7)'
    })
    axios({
      method: 'post',
      url: handleUrl(url),
      headers: {
        token: token,
        'Content-Type': 'multipart/form-data'
      },
      data: handleParams(data),
      dataType: 'json',
      processData: false,
      contentType: false
    }).then(
      (result) => {
        setTimeout(() => {
          loading.close()
        }, 1000)
        response(handleResults(result, data))
      }
    ).catch(
      (error) => {
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
        setTimeout(() => {
          loading.close()
        }, 1000)
      }
    )
  },
  /*
   * 下载文件用，导出 Excel 表格可以用这个方法
   * @param url
   * @param param
   * @param fileName 如果是导出 Excel 表格文件名后缀最好用.xls 而不是.xlsx，否则文件可能会因为格式错误导致无法打开
   * @param exception 异常的回调函数
   */
  downloadFile (url, data, fileName, exception) {
    axios({
      method: 'post',
      url: handleUrl(url),
      data: handleParams(data),
      responseType: 'blob'
    }).then(
      (result) => {
        const excelBlob = result.data
        if ('msSaveOrOpenBlob' in navigator) {
          // Microsoft Edge and Microsoft Internet Explorer 10-11
          window.navigator.msSaveOrOpenBlob(excelBlob, fileName)
        } else {
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          const blob = new Blob([excelBlob])
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          document.body.removeChild(elink)
        }
      }
    ).catch(
      (error) => {
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
      }
    )
  },
  uploadFileFormData (url, data, response, exception) {
    axios({
      method: 'post',
      url: handleUrl(url),
      data: data,
      timeout: TIME_OUT_MS,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then(
      (result) => {
        response(handleResults(result))
      }
    ).catch(
      (error) => {
        if (exception) {
          exception(error)
        } else {
          console.log(error)
        }
      }
    )
  }
}
