<template>
  <div class="projectTask">
    <div class='row banner' :style='{ width: sizeChange(1920), minHeight: sizeChange(232)}'>
      <div class="center-block">
        <div class='con-1' :style='{ fontSize: sizeChange(14), marginTop: sizeChange(20)}'>
          <i class="iconfont iconkecheng mr-2"></i>
          <span v-cloak>{{data.courseName}}</span><span>/</span><span v-cloak>{{data.projectName}}</span>
        </div>
        <div class='con-2' :style='{ fontSize: sizeChange(30), marginTop: sizeChange(21)}'>
          <span v-cloak>{{data.projectName}}</span>
        </div>
        <div class="con-3" :style='{ fontSize: sizeChange(14), marginTop: sizeChange(8), height:sizeChange(28)}' style="width: 70%">
          <span v-cloak></span>
        </div>
        <div class="con-3" :style='{ fontSize: sizeChange(14), marginTop: sizeChange(22),marginBottom: sizeChange(11)}'>
          <span class="mr-2">难度</span>
          <span class="mr-2" v-cloak>{{data.difficulty}}</span>
          <span class="mr-2">|</span>
          <span class="mr-2">时长</span>
          <span class="mr-2" v-cloak>{{data.suggestDays}}天</span>
<!--          <span class="mr-2">|</span>-->
<!--          <span class="mr-2">学习人数</span>-->
<!--          <span class="mr-2">10人</span>-->
          <span class="mr-2">|</span>
          <span class="mr-2">组队人数</span>
          <span class="mr-2" v-cloak>{{data.minCount}}~{{data.maxCount}}人</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ProjectTitle',
  props: {
    data: Object
  },
  data () {
    return {
      bodyWidth: $('body').width()
    }
  },
  mounted () {
    let me = this
    window.onresize = function () {
      me.bodyWidth = $('body').width()
    }
  },
  methods: {
    sizeChange (size) {
      return this.bodyWidth * size / 1920 + 'px'
    }
  }
}
</script>
<style scoped lang="scss">
.projectTask{
  .banner{
    background-size:100% 100%;
    -moz-background-size:100% 100%;
    background-repeat: no-repeat;
    background-image: url('img/banner2.png');
    color:#FFFFFF;
  }
}
</style>
