        <i class="iconfont iconxiaoxi notification" @click="jumpNotice">
          <span v-show="noticeIs" class="red-point"></span>
        </i>
        <el-dropdown @command="action">
          <span class="head-portrait el-dropdown-link"></span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
