<template>
  <div class="header">
    <div class="center-blockkk">
      <i class="iconfont iconlogo logo"></i>
      <span class="title" @click="jump">砺英网</span>
      <div class="pull-right">
<!--        <div class="search" :class="searchIsShow ? '' : 'hasInput'">-->
<!--          <input v-show="searchIsShow" ref="searchInput" class="search-input" type="text"/>-->
<!--          <a href="javascript:void(0);" @click="toggleInput" class="search-btn"><i class="iconfont iconzuishangfangsousuo"></i></a>-->
<!--        </div>-->
<!--        <span class="split"></span>-->
        <i class="iconfont iconhuo1 sns"><a :href=url  target="_blank">砺英社区—更多有趣的技术和Coder</a></i>
        <i class="iconfont iconxiaoxi notification" @click="jumpNotice">
          <span v-show="noticeIs" class="red-point"></span>
        </i>
        <el-dropdown @command="action">
          <span class="head-portrait el-dropdown-link"></span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Header',
  props: {
    noticeIs: {}
  },
  data () {
    return {
      searchIsShow: false,
      searchKey: '',
      url: this.$http.snsUrl + '/member/login?' + window.btoa(window.encodeURIComponent('snsNo=' + localStorage.getItem('snsNo')))
    }
  },
  methods: {
    toggleInput () {
      let me = this
      this.searchIsShow = !this.searchIsShow
      if (this.searchIsShow) {
        setTimeout(function () {
          me.$refs.searchInput.focus()
        })
      }
    },
    action (command) {
      if (command === 'logout') {
        localStorage.setItem('token', null)
        window.location = 'login.html'
      }
      if (command === 'userInfo') {
        window.location = 'personal-set.html'
      }
    },
    jump () {
      if (localStorage.getItem('userType') === '0') {
        window.location = 'personal-main.html'
      } else {
        window.location = 'assistant-main.html'
      }
    },
    jumpNotice () {
      window.location = 'notice.html'
    }
  }
}
</script>
<style scoped lang="scss">
  .red-point{
    position: relative;
  }

  .red-point::before{
    content: " ";
    border: 5px solid red;
    border-radius: 5px;
    position: absolute;
    z-index: 1000;
    right: 0;
    margin-right: -20px;
  }
  .header{
    /*height: 40px;*/
    height: 75px;
    background:rgba(41,41,41,1);
    color: #FFFFFF;
    display:block;
    .center-blockkk{
      width: 100%;
      padding-left: 5%;
      padding-right: 5%;
      div{
        height:100%;
        position:relative;
        float:left;
      }
      i, span{
        position:relative;
        float:left;
      }
      .logo{
        margin-top: 12px;
        font-size: 35px;
      }
      .title{
        font-size:20px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        margin-top: 25px;
        margin-left: 5px;
        cursor: pointer;
      }
      .search{
        width:320px;
        height:40px;
        background:#666666;
        padding-left:15px;
        .search-input{
          width:270px;
          height:40px;
          background:#666666;
          border: 0;
        }
        .search-input:focus{
          outline: none;
          border-color: #666666;
        }
        .search-btn{
          position:absolute;
          right:0px;
          width:40px;
          height:40px;
          padding: 9px 0 0 10px;
          color:#ffffff;
        }
      }
      .hasInput{
        background:none;
      }
      .split{
        width:1px;
        height:11px;
        background:rgba(255,255,255,1);
        margin-top: 14px;
        margin-left: 58px;
      }
      .notification{
        margin-top: 25px;
        font-size: 18px!important;
        /*margin-left: 128px;*/
      }
      .head-portrait{
        width:26px;
        height:26px;
        border-radius:50%;
        margin-top: 23px;
        margin-left: 30px;
        background-image: url("../../assets/img/user.png");
      }
      .sns{
        color:red;
        margin-top: 27px;
        font-size:16px;
        a{
          font-size: 12px;
          /*color:#ffffff;*/
          color:#f1f03d;
          margin-right: 50px;
          cursor: pointer;
        }
        a:hover{
          color: #6612cf;
        }
      }
    }
  }
</style>
