<template>
  <div class="teamTab">
    <div class="row">
      <div class="center-block">
        <div v-for="(obj, index) in data" v-bind:key="obj.name" :class="tabClass(index)">
          <div class="item" @click="goItem(obj)">{{index + 1 +'.'}}{{obj.name}}</div>
          <div class="triangle" v-if="index < data.length - 1"></div>
          <div class="blank" v-if="index < data.length - 1"></div>
        </div>
        <el-button v-if="expandButton === 1" @click="saveTeam" type="primary" class="pull-right">完成组队</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'TeamTab',
  props: {
    activeIndex: Number,
    data: Array,
    type: Number
  },
  data () {
    return {
      currentIndex: this.activeIndex,
      expandButton: this.type
    }
  },
  methods: {
    activeItem (id) {
      if (this.currentId === id) {
        this.currentId = -1
      } else {
        this.currentId = id
      }
    },
    tabClass (index) {
      if (index === 0) {
        return 'old'
      } else if (index === 1) {
        return 'current'
      } else {
        return ''
      }
    },
    saveTeam () {
      this.$emit('save-team')
    },
    goItem (obj) {
      if (obj.url) {
        window.location = obj.url
      }
    }
  }
}
</script>
<style scoped lang="scss">
.teamTab{
  .center-block{
    padding:20px 28px;
  }
  .item{
    position:relative;
    float:left;
    padding: 9px;
    font-size:12px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:10px;
    background:#F5F5F5;
    z-index: 100;
    width:153px;
    text-align: center;
    margin-left: -28px;
    cursor:pointer;
  }
  .old{
    .item{
      background-color: #00C366;
      color: #FFFFFF;
    }
    .triangle{
      border-color:transparent transparent transparent #00C366;
    }
  }
  .current{
    .item{
      background-color: #6612CF;
      color: #FFFFFF;
    }
    .triangle{
      border-color:transparent transparent transparent #6612CF;
    }
  }
  .triangle{
    position:relative;
    float:left;
    width: 0;
    height: 0;
    border-width: 14px;
    border-style: solid;
    z-index: 102;
    border-color:transparent transparent transparent #F5F5F5;
  }
  .blank{
    position:relative;
    float:left;
    width: 0;
    height: 0;
    border-width: 14px;
    border-style: solid;
    z-index: 101;
    border-color:transparent transparent transparent #FFFFFF;
    margin-left:-26px;
  }
}
</style>
